export const apiUrl = 'http://www.example.com/'

export const tokenPath = 'AUTH_TOKEN'

export const isProduction = process.env.NODE_ENV === 'production'

export const isSupportingBrowser = window.hasOwnProperty(
  'BeforeInstallPromptEvent'
)

export const isIOS =
  navigator.userAgent.includes('iPhone') ||
  navigator.userAgent.includes('iPad') ||
  (navigator.userAgent.includes('Macintosh') &&
    typeof navigator.maxTouchPoints === 'number' &&
    navigator.maxTouchPoints > 2)

export function getInstalledStatus(): boolean {
  const navigator2 = navigator as any

  if (!isProduction) return true

  if (navigator2.standalone) {
    return navigator2.standalone
  } else if (matchMedia('(display-mode: standalone)').matches) {
    return true
  } else {
    return false
  }
}
