import { IScalars } from '../client.types'

export enum TagType {
  Source = 'source',
  Content = 'content',
  Sentiment = 'sentiment',
}

export type ITagsQuery = {
  __typename?: 'TagsQuery'
  tags: Array<ITag>
}

export type ITag = {
  __typename?: 'Tag'
  value: IScalars['String']
  title: IScalars['String']
  type: TagType
}
