import React, { createRef, useState } from 'react'
import {
  Keyboard,
  KeyboardAvoidingView,
  ScrollView,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from 'react-native'
import { useMutation } from '@apollo/client'
import { ILoginMutation, LOGIN_MUTATION } from '../api'
import { tokenPath } from '../constants/Config'
import { RootTabScreenProps } from '../types'

export default function LoginScreen({
  navigation,
}: RootTabScreenProps<'Login'>) {
  const passwordInputRef = createRef<TextInput>()
  const [formState, setFormState] = useState({
    email: '',
    activationCode: '',
    error: '',
  })
  const [login] = useMutation<ILoginMutation>(LOGIN_MUTATION, {
    variables: {
      email: formState.email,
      activationCode: formState.activationCode,
    },
    onCompleted: ({ login }) => {
      localStorage.setItem(tokenPath, login?.token)
      navigation.navigate('Home')
    },
    onError: (error) => {
      setFormState({ ...formState, error: error.message })
    },
  })

  return (
    <View style={styles.mainBody}>
      <ScrollView
        keyboardShouldPersistTaps="handled"
        contentContainerStyle={{
          flex: 1,
          justifyContent: 'center',
          alignContent: 'center',
        }}
      >
        <View>
          <KeyboardAvoidingView enabled>
            <View style={styles.SectionStyle}>
              <TextInput
                style={styles.inputStyle}
                onChangeText={(email) =>
                  setFormState({
                    ...formState,
                    email,
                  })
                }
                placeholder="Email cím"
                placeholderTextColor="#8b9cb5"
                autoCapitalize="none"
                keyboardType="email-address"
                returnKeyType="next"
                onSubmitEditing={() =>
                  passwordInputRef.current && passwordInputRef.current.focus()
                }
                underlineColorAndroid="#f000"
                blurOnSubmit={false}
              />
            </View>
            <View style={styles.SectionStyle}>
              <TextInput
                style={styles.inputStyle}
                onChangeText={(activationCode) =>
                  setFormState({
                    ...formState,
                    activationCode,
                  })
                }
                placeholder="Aktivációs kód"
                placeholderTextColor="#8b9cb5"
                keyboardType="default"
                ref={passwordInputRef}
                onSubmitEditing={Keyboard.dismiss}
                blurOnSubmit={false}
                secureTextEntry={false}
                underlineColorAndroid="#f000"
                returnKeyType="next"
              />
            </View>
            {!!formState.error && (
              <Text style={styles.errorTextStyle} numberOfLines={3}>
                {formState.error}
              </Text>
            )}
            <TouchableOpacity
              style={styles.buttonStyle}
              activeOpacity={0.5}
              onPress={() => login()}
            >
              <Text style={styles.buttonTextStyle}>Aktiválás</Text>
            </TouchableOpacity>
          </KeyboardAvoidingView>
        </View>
      </ScrollView>
    </View>
  )
}

const styles = StyleSheet.create({
  mainBody: {
    flex: 1,
    justifyContent: 'center',
    alignContent: 'center',
  },
  SectionStyle: {
    flexDirection: 'row',
    height: 40,
    marginTop: 20,
    marginLeft: 35,
    marginRight: 35,
    margin: 10,
  },
  buttonStyle: {
    backgroundColor: '#7DE24E',
    //backgroundColor: '#61dafb',
    borderWidth: 0,
    color: '#FFFFFF',
    borderColor: '#7DE24E',
    height: 40,
    alignItems: 'center',
    borderRadius: 30,
    marginLeft: 35,
    marginRight: 35,
    marginTop: 20,
    marginBottom: 25,
  },
  buttonTextStyle: {
    color: '#FFFFFF',
    paddingVertical: 10,
    fontSize: 16,
  },
  inputStyle: {
    flex: 1,
    color: 'gray',
    paddingLeft: 15,
    paddingRight: 15,
    borderWidth: 1,
    borderRadius: 30,
    borderColor: '#dadae8',
  },
  registerTextStyle: {
    color: '#FFFFFF',
    textAlign: 'center',
    fontWeight: 'bold',
    fontSize: 14,
    alignSelf: 'center',
    padding: 10,
  },
  errorTextStyle: {
    color: 'red',
    textAlign: 'center',
    fontSize: 14,
  },
})
