import { makeExecutableSchema } from '@graphql-tools/schema'

import gql from 'graphql-tag'

const typeDefs = gql`
  type UserQuery {
    user: User!
  }

  type LoginResponse {
    token: String!
  }

  type LoginMutation {
    login: LoginResponse!
  }

  type Tag {
    title: String!
    value: String!
  }

  type User {
    name: String!
    email: String!
    phone: String!
    activationCode: String!
    preferredTags: [Tag!]!
    bookmarkedNews: [String!]!
    title: String!
    value: String!
  }
`

export const UsersSchema = makeExecutableSchema({ typeDefs })
