import * as Factory from 'factory.ts'
import faker from 'faker'
import { IUser, IUserQuery } from './users.types'
import { MockedResponse } from '@apollo/client/testing'
import { GET_USER } from './users.query'
import { contentTags } from '../tags/tags.mock'
import { articles } from '../articles/articles.mock'
import { LOGIN_MUTATION } from './users.query'
import { ILoginMutation } from '.'

export const UserMock = Factory.Sync.makeFactory<IUser>({
  name: Factory.each(
    () => `${faker.name.firstName()} ${faker.name.lastName()}`
  ),
  email: Factory.each(() => faker.random.words()),
  phone: Factory.each(() => faker.phone.phoneNumber()),
  activationCode: Factory.each(() => faker.random.word()),
  preferredTags: Factory.each(() =>
    faker.random.arrayElements(
      contentTags,
      faker.datatype.number({ min: 0, max: contentTags.length })
    )
  ),
  bookmarkedNews: Factory.each(() =>
    faker.random
      .arrayElements(
        articles,
        faker.datatype.number({ min: 0, max: articles.length })
      )
      .map((article) => article.articleId)
  ),
})

export const userQueryMock: Array<MockedResponse<IUserQuery | ILoginMutation>> =
  [
    {
      request: {
        query: GET_USER,
      },
      result: {
        data: {
          user: UserMock.build(),
        },
      },
    },
    {
      request: {
        query: LOGIN_MUTATION,
        variables: {
          email: '',
          activationCode: '',
        },
      },
      result: {
        data: {
          login: {
            token: Math.random().toString(36).slice(-5),
          },
        },
      },
    },
  ]
