import gql from "graphql-tag";

export const GET_ARTICLES = gql`
  query ArticleQuery($offset: Int, $limit: Int) {
    articles(offset: $offset, limit: $limit) {
      articleId
      title
      thumbnail
      breakingNews
      sendSMS
      sourceTag {
        title
        value
      }
      sentimentTag {
        title
        value
      }
      contentTag {
        title
        value
      }
      date
      url
    }
    total
  }
`;
