import { makeExecutableSchema } from '@graphql-tools/schema'

import gql from 'graphql-tag'

export const typeDefs = gql`
  enum TagType {
    Source
    Content
    Sentiment
  }

  type TagQuery {
    tags: [Tag!]!
  }

  type Tag {
    title: String!
    value: String!
    type: [TagType]!
  }
`

export const TagsSchema = makeExecutableSchema({ typeDefs })
