import * as Factory from 'factory.ts'
import faker from 'faker'
import { ITag, ITagsQuery } from './tags.types'
import { MockedResponse } from '@apollo/client/testing'
import { GET_TAGS } from './tags.query'
import { TagType } from './tags.types'
import { capitalize } from '../../utils'

export const ContentTagMock = Factory.Sync.makeFactory<ITag>({
  __typename: 'Tag',
  title: Factory.each(() => capitalize(faker.random.word())),
  value: Factory.each(
    () => `${faker.random.word()}_${Math.random().toString(36).slice(-5)}`
  ),
  type: TagType.Content,
})

export const SourceTagMock = Factory.Sync.makeFactory<ITag>({
  __typename: 'Tag',
  title: Factory.each(() => capitalize(faker.random.word())),
  value: Factory.each(
    () => `${faker.random.word()}_${Math.random().toString(36).slice(-5)}`
  ),
  type: TagType.Source,
})

export const sourceTags = SourceTagMock.buildList(20)

export const contentTags = ContentTagMock.buildList(20)

export const sentimentTags = [
  {
    title: 'Good',
    value: 'good',
    type: TagType.Sentiment,
  },
  {
    title: 'Neutral',
    value: 'neutral',
    type: TagType.Sentiment,
  },
  {
    title: 'Bad',
    value: 'bad',
    type: TagType.Sentiment,
  },
]

export const tagsQueryMock: Array<MockedResponse<ITagsQuery>> = [
  {
    request: {
      query: GET_TAGS,
      variables: {
        type: TagType.Content,
      },
    },
    result: {
      data: {
        tags: contentTags,
      },
    },
  },
  {
    request: {
      query: GET_TAGS,
      variables: {
        type: TagType.Source,
      },
    },
    result: {
      data: {
        tags: sourceTags,
      },
    },
  },
  {
    request: {
      query: GET_TAGS,
      variables: {
        type: TagType.Sentiment,
      },
    },
    result: {
      data: {
        tags: sentimentTags,
      },
    },
  },
]
