import {
  ApolloClient,
  ApolloLink,
  InMemoryCache,
  createHttpLink,
} from '@apollo/client'
import { setContext } from '@apollo/client/link/context'
import { apiUrl, tokenPath } from '../constants/Config'
import { schemaLink } from './client.schema'

export const cache = new InMemoryCache({
  typePolicies: {
    Query: {
      fields: {
        articles: {
          // extracted from '@apollo/client/utilities'
          keyArgs: false,
          merge(existing, incoming, { args }) {
            const merged = existing ? existing.slice(0) : []
            const start = args ? args.offset : merged.length
            const end = start + incoming.length
            for (let i = start; i < end; ++i) {
              merged[i] = incoming[i - start]
            }
            return merged
          },
        },
      },
    },
  },
})

const link = createHttpLink({ uri: apiUrl })

const authLink = setContext((_, { headers }) => {
  const token = localStorage.getItem(tokenPath)
  return {
    headers: {
      ...headers,
      authorization: token ? `Bearer ${token}` : '',
    },
  }
})

export const client = new ApolloClient({
  connectToDevTools: true,
  link: ApolloLink.from([authLink, schemaLink, link]),
  cache,
  resolvers: {},
  defaultOptions: {
    query: {
      errorPolicy: 'all',
    },
  },
})
