import React, { FunctionComponent } from 'react'
import { MockedProvider } from '@apollo/client/testing'
import { ApolloProvider } from '@apollo/client'
import { client, cache } from './client'
import { articlesQueryMock } from './articles/articles.mock'
import { tagsQueryMock } from './tags/tags.mock'
import { userQueryMock } from './users/users.mock'

interface ProviderProps {
  useMocks?: boolean
}

export const ClientProvider: FunctionComponent<ProviderProps> = ({
  useMocks,
  children,
}) => {
  if (useMocks)
    return (
      <MockedProvider
        mocks={[...articlesQueryMock, ...tagsQueryMock, ...userQueryMock]}
        cache={cache}
      >
        {children}
      </MockedProvider>
    )
  return (
    <ApolloProvider client={client}>
      <>{children}</>
    </ApolloProvider>
  )
}
