import React from 'react'
import { useQuery } from '@apollo/client'
import { GET_TAGS, ITag, ITagsQuery, TagType } from '../api'

const SourceTagsContext = React.createContext<Array<ITag> | null>(null)

export interface ISourceTagsProps {
  children?: JSX.Element
}

export const SourceTagsProvider = (props: ISourceTagsProps) => {
  const { data } = useQuery<ITagsQuery>(GET_TAGS, {
    variables: { type: TagType.Source },
  })
  const sourceTags = data?.tags || null
  return (
    <SourceTagsContext.Provider value={sourceTags}>
      {props.children}
    </SourceTagsContext.Provider>
  )
}

export const SourceTagsConsumer = SourceTagsContext.Consumer
