import React from 'react'
import { createNativeStackNavigator } from '@react-navigation/native-stack'
import { FunctionComponent } from 'react'
import { DropdownList } from '../components/DropdownList'
import NotFoundScreen from '../screens/NotFoundScreen'
import SettingsScreen from '../screens/SettingsScreen'
import HelpScreen from '../screens/HelpScreen'
import { RootStackParamList } from '../types'
import { TopTabNavigator } from './TopTabNavigator'
import DetailsScreen from '../screens/DetailsScreen'
import { StyleSheet } from 'react-native'

import {
  ContentTagsProvider,
  SentimentTagsProvider,
  SourceTagsProvider,
  UserProvider,
} from '../context'
import { ClientProvider } from '../api'
import Colors from '../constants/Colors'
import { NavbarIconButton } from '../components/NavbarIconButton'

const Stack = createNativeStackNavigator<RootStackParamList>()

export interface IRootNavigatorProps {}

const navbarIcons = {
  SETTINGS: {
    name: 'settings',
    direction: 'Settings',
  },
  HELP: {
    name: 'help-outline',
    direction: 'Help',
  },
  BACK: {
    name: 'keyboard-arrow-left',
    direction: 'Root',
  },
}

const styles = StyleSheet.create({
  screenOptionsHeader: {
    height: 50,
    backgroundColor: Colors.oxfordBlue,
    color: Colors.orangeWeb,
  },
  screenOptionsHeaderTitle: {
    color: Colors.orangeWeb,
    fontFamily: 'Roboto_500Medium',
    fontSize: 18,
  },
})

export const RootNavigator: FunctionComponent<IRootNavigatorProps> = () => {
  return (
    <ClientProvider useMocks>
      <UserProvider>
        <SourceTagsProvider>
          <SentimentTagsProvider>
            <ContentTagsProvider>
              <Stack.Navigator
                screenOptions={{
                  headerStyle: styles.screenOptionsHeader,
                  headerTitleStyle: styles.screenOptionsHeaderTitle,
                  headerTitleAlign: 'center',
                }}
              >
                <Stack.Screen
                  name="Root"
                  component={TopTabNavigator}
                  options={({ navigation }) => ({
                    title: '',
                    headerLeft: () => (
                      <NavbarIconButton
                        navigation={navigation}
                        icons={[navbarIcons.SETTINGS, navbarIcons.HELP]}
                      />
                    ),
                    headerRight: () => <DropdownList />,
                  })}
                />
                <Stack.Screen
                  name="NotFound"
                  component={NotFoundScreen}
                  options={({ navigation }) => ({
                    title: 'Oops!',
                    headerLeft: () => (
                      <NavbarIconButton
                        navigation={navigation}
                        icons={[navbarIcons.BACK]}
                      />
                    ),
                  })}
                />
                <Stack.Screen
                  name="Settings"
                  component={SettingsScreen}
                  options={({ navigation }) => ({
                    title: 'Beállítások',
                    headerLeft: () => (
                      <NavbarIconButton
                        navigation={navigation}
                        icons={[navbarIcons.BACK]}
                      />
                    ),
                  })}
                />
                <Stack.Screen
                  name="Help"
                  component={HelpScreen}
                  options={({ navigation }) => ({
                    title: 'Segítség',
                    headerLeft: () => (
                      <NavbarIconButton
                        navigation={navigation}
                        icons={[navbarIcons.BACK]}
                      />
                    ),
                  })}
                />
                <Stack.Screen
                  name="Details"
                  component={DetailsScreen}
                  options={({ navigation }) => ({
                    title: 'Cikk',
                    headerLeft: () => (
                      <NavbarIconButton
                        navigation={navigation}
                        icons={[navbarIcons.BACK]}
                      />
                    ),
                  })}
                />
              </Stack.Navigator>
            </ContentTagsProvider>
          </SentimentTagsProvider>
        </SourceTagsProvider>
      </UserProvider>
    </ClientProvider>
  )
}
