import { makeExecutableSchema } from "@graphql-tools/schema";

import gql from "graphql-tag";

const typeDefs = gql`
  type ArticleQuery {
    articles: [Article!]!
    total: Int!
  }

  type Tag {
    title: String!
    value: String!
  }

  type Article {
    articleId: ID!
    title: String!
    thumbnail: String!
    breakingNews: Boolean!
    sendSMS: Boolean!
    sourceTag: Tag!
    sentimentTag: Tag!
    contentTag: [Tag!]!
    date: String!
    url: String!
  }
`;

export const ArticlesSchema = makeExecutableSchema({ typeDefs });
