import React, { FunctionComponent } from 'react'
import { View, StyleSheet } from 'react-native'
import Colors from '../constants/Colors'
import Icon from 'react-native-vector-icons/MaterialIcons'

export interface INavbarIcon {
  name: string
  direction: string
}

export interface INavbarIconButtonProps {
  navigation: any
  icons: INavbarIcon[]
}

const styles = StyleSheet.create({
  icon: {
    margin: 6,
  },
  iconWrapper: {
    flexDirection: 'row',
    alignItems: 'center',
    marginLeft: 7,
  },
})

export const NavbarIconButton: FunctionComponent<INavbarIconButtonProps> = (
  props
) => {
  const { navigation, icons } = props
  return (
    <View style={styles.iconWrapper}>
      {icons.map((icon, i) => (
        <Icon
          key={i}
          name={icon.name}
          size={24}
          color={Colors.orangeWeb}
          onPress={() => navigation.navigate(icon.direction)}
          style={styles.icon}
        />
      ))}
    </View>
  )
}
