import * as React from 'react'
import { StyleSheet } from 'react-native'
import { Card } from 'react-native-elements'
import { Text, View } from '../components/Themed'

export default function InstallScreen() {
  return (
    <View style={styles.container}>
      <Card containerStyle={styles.card}>
        <Card.Title style={styles.title}>Hibás művelet!</Card.Title>
        <Card.Divider />
        <View>
          <Text style={styles.text}>
            Több okból is láthatja ezt a képernyőt:
          </Text>
          <View style={styles.list}>
            <Text style={styles.text}>
              {`\u2022 Ön már installálta az alkalmazást`}
            </Text>
            <Text style={styles.text}>
              {`\u2022 Az ön eszköze nem teszi lehetővé az alkalmazás telepítését`}
            </Text>
            <Text style={styles.text}>
              {`\u2022 Ön böngészőből próbálja meg elérni az alkalmazást`}
            </Text>
          </View>
          <Text style={styles.text}>
            Kérjük amennyiben további kérdése maradt lépjen kapcsolatba
            ügyfélszolgálatunkkal!
          </Text>
        </View>
      </Card>
    </View>
  )
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
    padding: 20,
    backgroundColor: 'gray',
  },
  card: {
    width: '80%',
  },
  list: {
    marginLeft: 10,
    marginVertical: 10,
  },
  title: {
    fontSize: 20,
    fontWeight: 'bold',
  },
  text: {
    fontSize: 14,
    color: 'gray',
  },
})
