// Example of Splash, Login and Sign Up in React Native
// https://aboutreact.com/react-native-login-and-signup/
import 'react-native-gesture-handler'

// Import React and Component
import React, { FunctionComponent, useState } from 'react'

// Import Navigators from React Navigation
import {
  DarkTheme,
  DefaultTheme,
  NavigationContainer,
} from '@react-navigation/native'
import { createNativeStackNavigator } from '@react-navigation/native-stack'

import LoginScreen from '../screens/LoginScreen'
import { ClientProvider } from '../api'
import { tokenPath } from '../constants/Config'
import LinkingConfiguration from './LinkingConfiguration'
import { RootNavigator } from './RootNavigator'

const Stack = createNativeStackNavigator()

export interface IAuthNavigationProps {}

export const AuthNavigation: FunctionComponent<IAuthNavigationProps> = ({}) => {
  const [token] = useState(() => {
    return localStorage.getItem(tokenPath)
  })

  return (
    <ClientProvider useMocks>
      <NavigationContainer linking={LinkingConfiguration} theme={DefaultTheme}>
        <Stack.Navigator
          initialRouteName={token ? 'Home' : 'Login'}
          screenOptions={{
            header: () => null,
          }}
        >
          <Stack.Screen name="Home" component={RootNavigator} />
          <Stack.Screen
            name="Login"
            component={LoginScreen}
            options={{ headerShown: false }}
          />
        </Stack.Navigator>
      </NavigationContainer>
    </ClientProvider>
  )
}
