import gql from 'graphql-tag'

export const GET_USER = gql`
  query getUser {
    user {
      name
      email
      phone
      activationCode
      preferredTags {
        title
        value
      }
      bookmarkedNews
    }
  }
`

export const LOGIN_MUTATION = gql`
  mutation LoginMutation($email: String!, $activationCode: String!) {
    login(email: $email, activationCode: $activationCode) {
      token
    }
  }
`
