import React from 'react'
import { useQuery } from '@apollo/client'
import { GET_TAGS, ITag, ITagsQuery, TagType } from '../api'

const SentimentTagsContext = React.createContext<Array<ITag> | null>(null)

export interface ISentimentTagsProps {
  children?: JSX.Element
}

export const SentimentTagsProvider = (props: ISentimentTagsProps) => {
  const { data } = useQuery<ITagsQuery>(GET_TAGS, {
    variables: { type: TagType.Sentiment },
  })
  const sentimentTags = data?.tags || null
  return (
    <SentimentTagsContext.Provider value={sentimentTags}>
      {props.children}
    </SentimentTagsContext.Provider>
  )
}

export const SentimentTagsConsumer = SentimentTagsContext.Consumer
