import * as Factory from 'factory.ts'
import faker from 'faker'
import { IArticle, IArticleQuery } from './articles.types'
import { MockedResponse } from '@apollo/client/testing'
import { GET_ARTICLES } from './articles.query'
import { sourceTags, sentimentTags, contentTags } from '../tags/tags.mock'

const articleNumber = 30
const articlePerPage = 10

export const ArticleMock = Factory.Sync.makeFactory<IArticle>({
  __typename: 'Article',
  articleId: Factory.each(() => faker.datatype.uuid()),
  title: Factory.each(() => faker.random.words()),
  thumbnail: Factory.each(() => faker.image.avatar()),
  breakingNews: false,
  sendSMS: false,
  sourceTag: Factory.each(() => faker.random.arrayElement(sourceTags)),
  sentimentTag: Factory.each(() => faker.random.arrayElement(sentimentTags)),
  contentTag: Factory.each(() =>
    faker.random.arrayElements(
      contentTags,
      faker.datatype.number({ min: 1, max: 5 })
    )
  ),
  date: Factory.each(() => faker.date.past().toISOString()),
  url: Factory.each(() => faker.random.words()),
})

export const articles = ArticleMock.buildList(articleNumber)

export const articlesQueryMock: Array<MockedResponse<IArticleQuery>> = [
  ...[...Array(Math.ceil(articleNumber / articlePerPage)).keys()].map((i) => {
    const data = {
      __typename: 'ArticleQuery' as 'ArticleQuery',
      total: articleNumber,
      articles: [...articles].splice(i * articlePerPage, articlePerPage),
    }

    return {
      request: {
        query: GET_ARTICLES,
        variables: {
          offset: i * articlePerPage,
          limit: articlePerPage,
        },
      },
      result: {
        data,
      },
      newData: () => ({
        data,
      }),
    }
  }),
]
