import React, { FunctionComponent, useState } from "react";
import { MultiSelect } from "react-native-element-dropdown";
import Colors from "../constants/Colors";
import Icon from "react-native-vector-icons/MaterialIcons";
import { View, Text, StyleSheet } from "react-native";

export interface IDropdownListProps { }

interface SourceFilter {
    label: string;
    value: string;
}

const SHORT_LINE_LENGTH = 15;
const ALL_SOURCES = "Minden forrás";
const MANY_SOURCES = "Több forrás";
const ROBOTO_400REGULAR = "Roboto_400Regular";
const ROBOTO_300LIGHT = "Roboto_300Light";
const MAX_HEIGHT = 300;

const styles = StyleSheet.create({
    dropdown: {
        height: 24,
        width: 170,
        marginRight: 13
    },
    placeholder: {
        color: Colors.orangeWeb,
        fontSize: 16,
        marginLeft: 8,
        marginRight: 8,
    },
    dropdownContainer: {
        alignSelf: "flex-start",
        width: 146,
        borderColor: Colors.oxfordBlue,
        borderBottomStartRadius: 10,
        borderBottomEndRadius: 10,
    },
    dropdownContainerTextWrap: {
        flex: 1,
        flexDirection: "row",
        height: 25,
        margin: 8,
    },
    dropdownContainerText: {
        color: Colors.oxfordBlue,
        fontSize: 14,
    },
    selected: {
        fontFamily: ROBOTO_400REGULAR,
        width: "90%",
    },
    unselected: {
        fontFamily: ROBOTO_300LIGHT,
    },
});

const createPlaceholderFromSelected = (selected: string[], sourceFilters: SourceFilter[]) => {
    const word = sourceFilters.find((item) => item.value === selected[0])?.label!
    if (word.length < SHORT_LINE_LENGTH) return word;
    const subWord = word.substr(0, SHORT_LINE_LENGTH);
    return subWord.substr(0, Math.min(subWord.length, subWord.lastIndexOf(" "))) + " ...";
};

const renderRightIcon = () => <Icon name={"keyboard-arrow-down"} size={24} color={Colors.orangeWeb} />;

export const DropdownList: FunctionComponent<IDropdownListProps> = () => {
    const sourceFilters = [
        { label: 'Source Source 1 Source 1', value: '1' },
        { label: 'Source 2', value: '2' },
        { label: 'Source 3', value: '3' },
        { label: 'Source 4', value: '4' },
        { label: 'Source 5', value: '5' },
        { label: 'Source 6', value: '6' },
        { label: 'Source 7', value: '7' },
        { label: 'Source 8', value: '8' },
        { label: 'Source 9', value: '9' },
        { label: 'Source 10', value: '10' },
        { label: 'Source 11', value: '11' },
        { label: 'Source 12', value: '12' },
        { label: 'Source 13', value: '13' },
        { label: 'Source 14', value: '14' },
        { label: 'Source 15', value: '15' },
    ];
    const [selected, setSelected] = useState([]);
    const [placeholder, setPlaceholder] = useState(ALL_SOURCES);

    const renderItem = (item: SourceFilter) => {
        const isSelected = selected.find((sel) => sel === item.value);
        return (
            <View style={styles.dropdownContainerTextWrap}>
                <Text numberOfLines={1} style={[styles.dropdownContainerText, isSelected ? styles.selected : styles.unselected]}>
                    {item.label}
                </Text>
                {isSelected && <Icon name="check" size={16} />}
            </View>
        );
    };

    const onChange = (item: []) => {
        setSelected(item);
        setPlaceholder(!selected.length ? ALL_SOURCES : selected.length > 1 ? MANY_SOURCES : createPlaceholderFromSelected(selected, sourceFilters));
    };

    return (
        <View>
            <MultiSelect
                data={sourceFilters}
                placeholder={placeholder}
                labelField="label"
                valueField="value"
                value={selected}
                onChange={onChange}
                renderRightIcon={renderRightIcon}
                renderItem={renderItem}
                renderSelectedItem={() => <></>}
                style={styles.dropdown}
                maxHeight={MAX_HEIGHT}
                showsVerticalScrollIndicator={false}
                fontFamily={ROBOTO_400REGULAR}
                placeholderStyle={styles.placeholder}
                containerStyle={styles.dropdownContainer}
                activeColor={Colors.unselectedBlue + 20}
            />
        </View>
    );
};
