import React from 'react'
import { useQuery } from '@apollo/client'
import { GET_TAGS, ITag, ITagsQuery, TagType } from '../api'

const ContentTagsContext = React.createContext<Array<ITag> | null>(null)

export interface IContentTagsProps {
  children?: JSX.Element
}

export const ContentTagsProvider = (props: IContentTagsProps) => {
  const { data } = useQuery<ITagsQuery>(GET_TAGS, {
    variables: { type: TagType.Content },
  })
  const contentTags = data?.tags || null
  return (
    <ContentTagsContext.Provider value={contentTags}>
      {props.children}
    </ContentTagsContext.Provider>
  )
}

export const ContentTagsConsumer = ContentTagsContext.Consumer
